import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip, Col } from "antd";
//import NumberFormat from "../../../App/NumberFormat";
import NumberFormat from "../../../App/NumberFormat";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/cashBank/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class CashBank extends Component {
  state = {
    filter: {
      id_account: [],
      id_location: [],
      is_active: null,
      sort_by: "cash_bank_code",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.cashBank.error) {
      notification("error", this.props.cashBank.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.cashBank.success) {
      notification("success", this.props.cashBank.success);
      this.props.clearSuccess();
    }
  }

  // Get All data diawal
  componentDidMount() {
    const { getCashBanks } = this.props;
    getCashBanks();

    //console.log(this.state.filter);
  }

  // Handle search
  handleSearch = value => {
    const { getCashBanks } = this.props;
    this.setState({
      filter: {
        id_account: [],
        id_location: [],
        is_active: null,
        sort_by: "cash_bank_code",
        sort_type: "asc",
        pagination: null
      }
    });
    getCashBanks(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getCashBanks } = this.props;
    this.setState({
      filter: {
        id_account: [],
        id_location: [],
        is_active: null,
        sort_by: "cash_bank_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getCashBanks();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getCashBanks } = this.props;
    //const { current_page } = this.props.cashBank;
    const { filter } = this.state;
    let string = "";

    Object.keys(filter).map(key =>
      filter[key]
        ? string
          ? (string += `&${key}=${filter[key]}`)
          : (string += `${key}=${filter[key]}`)
        : null
    );

    this.setState({ search: null });
    getCashBanks(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getCashBanks } = this.props;
    this.setState({
      filter: {
        id_account: [],
        id_location: [],
        is_active: null,
        sort_by: "cash_bank_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getCashBanks();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateCashBank(id);
    } else {
      this.props.deactivateCashBank(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCashBanks } = this.props;
    const { search } = this.props.cashBank;
    const { filter } = this.props.cashBank;
    getCashBanks(page, search, filter);
  };

  render() {
    // console.log(this.props.cashBank);

    // Data setup
    const { cash_banks } = this.props.cashBank;
    //console.log(this.props.cashBank); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "cash_bank_code",
        rowKey: "cash_bank_code",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "description",
        rowKey: "description",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "cashBanks.add.bankAccountNumber" }),
        dataIndex: "bank_account_number",
        rowKey: "bank_account_number",
        width: "15%",
        render: text => {
          if (text) {
            return <span>{text}</span>;
          } else {
            return <span>{}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location",
        rowKey: "location",
        width: "20%",
        render: (text, row) => {
          if (text) {
            return <span>{text.location_name}</span>;
          } else {
            return <span>{}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "items.tab.account" }),
        dataIndex: "account",
        rowKey: "account",
        width: "20%",
        render: (text, row) => {
          return (
            <span>
              {text && text.account_code + " (" + text.account_name + ")"}
            </span>
          );
        }
      },
      {
        title: getLanguage({ id: "currencies.pageHeader" }),
        dataIndex: "currency",
        rowKey: "currency",
        width: "10%",
        align: "center",
        render: (text, row) => {
          if (row.account && row.account.currency) {
            return <span>{row.account.currency.currency_code}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "openingBalance" }),
        dataIndex: "opening_balance",
        rowKey: "opening_balance",
        width: "10%",
        align: "right",
        render: text => {
          return <NumberFormat value={parseFloat(text)} />;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    let listAction = {
      title: "",
      dataIndex: "id_cash_bank",
      rowKey: "id_cash_bank",
      fixed: "right",
      align: "center",
      render: (text, row) => (
        <div>
          <Tooltip
            placement="topLeft"
            title={<IntlMessages id="tooltip.update" />}
          >
            <Link to={`${this.props.match.path}/update/${text}`}>
              <Button size="small" className="invoiceEdtBtn">
                <i className="ion-android-create" />
              </Button>
            </Link>
          </Tooltip>

          {row.is_active ? (
            <Popconfirms
              title={<IntlMessages id="cashBanks.popconfirm.deactivate" />}
              onConfirm={this.handleRecord.bind(this, "deactivate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.deactivate" />}
              >
                <Button size="small" className="invoiceDltBtn">
                  <i className="ion-close-circled" />
                </Button>
              </Tooltip>
            </Popconfirms>
          ) : (
            <Popconfirms
              title={<IntlMessages id="cashBanks.popconfirm.activate" />}
              onConfirm={this.handleRecord.bind(this, "activate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.activate" />}
              >
                <Button size="small" className="invoiceRstBtn">
                  <i className="ion-refresh" />
                </Button>
              </Tooltip>
            </Popconfirms>
          )}
        </div>
      )
    };
    //console.log(permissions);
    if (permissions.includes("Update Cash Bank") && columns.length === 8) {
      columns.push(listAction);
    }

    Object.keys(cash_banks).map((cashBank, index) => {
      return dataSource.push({
        ...cash_banks[cashBank],
        key: cashBank
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.cashBank;

    // console.log(page_size);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="cashBanks.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Cash Bank") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="cashBanks.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) +
                ", " +
                getLanguage({ id: "cashBanks.add.bankAccountNumber" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="accounts.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_account"
                      )}
                      value={
                        this.state.filter.id_account
                          ? this.state.filter.id_account
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="cashBanks.filter.select.account" />
                      }
                      loading={this.props.cashBank.loading}
                      allowClear={true}
                    >
                      {this.props.cashBank.accounts.map((data, key) =>
                        data.id_account_type === "02" ? (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </div>
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="cashBanks.filter.select.location" />
                      }
                      loading={this.props.cashBank.loading}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {this.props.cashBank.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <Col span={12}>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="sortBy" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(this, "sort_by")}
                        value={
                          this.state.filter.sort_by
                            ? this.state.filter.sort_by
                            : null
                        }
                        style={{ width: "90%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.cashBank.loading}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value="cash_bank_code">
                          <IntlMessages id="code" />
                        </Option>
                        <Option key={2} value="description">
                          <IntlMessages id="name" />
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8}>
                      <label>
                        <IntlMessages id="sortType" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "sort_type"
                        )}
                        value={
                          this.state.filter.sort_type
                            ? this.state.filter.sort_type
                            : null
                        }
                        style={{ width: "90%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.cashBank.loading}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value="asc">
                          <IntlMessages id="ascending" />
                        </Option>
                        <Option key={2} value="desc">
                          <IntlMessages id="descending" />
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8}>
                      <label>
                        <IntlMessages id="status" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "is_active"
                        )}
                        value={
                          this.state.filter.is_active
                            ? this.state.filter.is_active
                            : null
                        }
                        style={{ width: "85%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.cashBank.loading}
                        allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value={1}>
                          {getLanguage({ id: "active" })}
                        </Option>
                        <Option key={3} value={"0"}>
                          {getLanguage({ id: "inactive" })}
                        </Option>
                      </Select>
                    </Col>
                  </Col>

                  <Col span={12} style={{ display: "flex" }}>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="dataPerPage" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "pagination"
                        )}
                        value={
                          this.state.filter.pagination
                            ? this.state.filter.pagination
                            : page_size
                        }
                        style={{ width: "95%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.cashBank.loading}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value={10}>
                          10
                        </Option>
                        <Option key={2} value={25}>
                          25
                        </Option>
                        <Option key={3} value={50}>
                          50
                        </Option>
                        <Option key={4} value={100}>
                          100
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8} style={{ marginTop: "auto" }}>
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.submit" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {cash_banks.length === 0 && !this.props.cashBank.loading ? (
              <Empty
                description={<IntlMessages id="cashBanks.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.cashBank.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    cashBank: state.cashBank,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(CashBank);
